<template lang="pug">
div.article-add.common-block
	Form(
		ref="form"
		v-if="!mediaId"
		:model="formData"
		:label-width="130"
	)
		h2 
		FormItem(label="" prop="title")
			Input(
				type="text"
				v-model="formData.title"
				placeholder=""
			)
		FormItem(label="ID" prop="cover")
			Input(
				type="text"
				v-model="formData.thumb_media_id"
				placeholder="ID"
			)
		FormItem(label="")
			Input(
				type="text"
				v-model="formData.author"
				placeholder=""
			)
		FormItem(label="")
			Input(
				type="text"
				v-model="formData.digest"
				placeholder=""
			)
		FormItem(label="")
			RadioGroup(v-model="formData.show_cover_pic")
				Radio(label="true") 
				Radio(label="false") 
		FormItem(label=" " prop="content")
			Input(
				type="textarea"
				placeholder=""
				v-model="formData.content"
				:autosize="{minRows: 4}"
			)
		FormItem(label="" prop="address")
			Input(
				type="text"
				v-model="formData.content_source_url"
				placeholder="URL"
			)
		FormItem
			Button(
				type="success"
				shape="circle"
				size="small"
				@click="onSubmit"
			) 
			Button(
				type="ghost"
				shape="circle"
				size="small"
				@click="$router.go(-1)"
				class="u-ml10"
			) 
	div.article-add__result(v-else)
		h2.u-color-green 
		p.u-ta-center
			span.u-mr10.u-color-main {{mediaId}}
			span.u-td-underline.u-color-blue.u-pointer(
				v-clipboard="mediaId"
				@success="copySuccessFn"
			) 
</template>

<script>
export default {
	data () {
		return {
			formData: {
				show_cover_pic: 'false',
			},
			mediaId: '',
			copySuccessFn: require('src/common/utils').copySuccessFn,
		}
	},

	methods: {
		onSubmit () {
			const items = [
				'title',
				'thumb_media_id',
				'content',
				'content_source_url',
			]
			if (items.some(o => this.formData[o] === '')) {
				this.$Message.error('')
				return
			}

			this.$post('/wx-manager/api/article/add', this.formData)
				.then(data => this.mediaId = data.media_id)
		},
	},
}
</script>

<style lang="stylus" scoped>
.article-add
	h2
		text-align: center
		margin-bottom: 1rem
	button
		min-width 3rem
</style>



